body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-image: url('/images/final-fantasy-vii-hd-wallpapers-33116-3206975.jpg');
  /* background-position: center; */
  background-size: cover;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.login{
  display: inline-block; padding: 32px 48px 0px 48px; border: 1px solid #EEE;  
}

.home{
    border: 1px solid purple;
}

.home img{
    max-width: 100%;
}

.logo-search-header img{
    height: 90px;
}

.header{
    color: #969696
}

.header .login-nav-bar{
    height: 30px;
}

.header .btn-github{
	background-image: url("/images/GitHub-Mark-Light-32px.png") !important;
	color: #fff;
    background-color: #444;
	border-color: rgba(0,0,0,0.2);
	background-repeat: no-repeat;
	background-position: 5px;
}
.btn-github:hover{
	color: rgba(200,200,200,0.5);
}
.game-container{
    background:#fff;
    padding:35px 20px 20px;
    margin:50px 0;
}

.game-container h1{
    font-family:'ZCOOL QingKe HuangYou',cursive;
    font-size:5rem;
}

.game-pic{
    width:95%;
}

.game-title, .game-desc{
    margin:0;
}

@media screen and (max-width:600px){
    .game-pic{
        width:80%;
    }

    .game-title{
        margin-top:10px;
    }
}

@media screen and (min-width:600px) and (max-width:1200px){
    .game-desc{
        width:80%;
        margin:0 auto;
    }
}
